import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { AuthService } from '../shared/services/auth.service';
import { GameService } from '../shared/services/game.service';
import { NotifierService } from '../shared/notifier/notifier.service';

@Component({
	selector: 'app-result',
	templateUrl: './result.component.html',
	styleUrls: ['./result.component.css']
})
export class ResultComponent implements OnInit {
	gameId:string;
	game:any;
	thisUserStatus:any;
	result:string;
	constructor(private activatedRoute:ActivatedRoute, private gameService: GameService, private notifier: NotifierService, private authService: AuthService) {
		window.localStorage.removeItem("game");

	 }

	ngOnInit() {
		this.activatedRoute.params.subscribe((params: Params) => {
			this.gameId = params['gameId'];
			this.loadGame();
		});
	}

	loadGame(){
		let thisUser=this.authService.user();
		if(!thisUser){
			console.log("session expired");
			return;
		}
		this.gameService.getFinishedGame(this.gameId).subscribe(
			res=>{
				
				let thisUserSide=null;
				if(thisUser.id==res.playerB){
					thisUserSide="B";
				}else if(thisUser.id==res.playerA){
					thisUserSide="A";
				}else{
					alert("You did not belong to this game!");
					return;
				}
				this.game=res;
				let scoreObj={"A":'scoreA', B:"scoreB"};
				let scoreObjOpp={"B":'scoreA', A:"scoreB"};
				if(res[scoreObj[thisUserSide]]>res[scoreObjOpp[thisUserSide]]){
					this.result="won"
				}else{
					this.result="lost";
				}
			},
			err=>{
				this.notifier.alert(err.code, err.message, "danger", 2000);
			}
		)
	}

}



